import { Directive, HostListener } from '@angular/core';
import { MatChip } from '@angular/material/chips';


@Directive({
    selector: 'mat-chip[i360-chip]',
})
export class ExtendMatChipDirective {
    constructor(private chip: MatChip) {
        this.chip.selectable = false;
    }

    @HostListener('click', ['$event']) onClick(event: Event) {
        // remove the chip on click anywhere, not only the "close" icon
        if (!/\bmat-chip-remove\b/.test((<HTMLElement>event.target).className)) {
            this.chip.remove();
        }
    }
}
