import { Component, OnDestroy } from '@angular/core';
import { Mode } from '@imunify360-api/proactive-defense';
import { I360Config } from 'app/utils/config/config';
import { AuthState } from 'app/services/auth-state';
import { MyImunifyService } from 'app/services/my-imunify';
import { combineLatest, Subject, takeUntil } from 'rxjs';
import { IMyImunifyState } from 'app/services/my-imunify-state';
import { AdminConfigType } from 'app/utils/config/admin-config';

@Component({
    selector: 'i360-mode',
    templateUrl: './mode.component.html',
    styleUrls: ['./mode.component.scss'],
})
export class ModeComponent implements OnDestroy {
    Mode = Mode;
    public disableMode: boolean;
    public disableMessage: string;

    private destroy$ = new Subject<void>();

    constructor(
        public config: I360Config,
        public authState: AuthState,
        private myImunifyService: MyImunifyService,
    ) {
        combineLatest([this.config.changes, this.myImunifyService.getMyImunifyState(), this.authState.isClient])
            .pipe(takeUntil(this.destroy$))
            .subscribe(([config, myImunifyState, isClient]: [AdminConfigType, IMyImunifyState, boolean]) => {
                this.updateDisabledState(config, myImunifyState, isClient);
            });
    }

    ngOnDestroy(): void {
        this.destroy$.next();
    }

    changeModeHandler(element: HTMLDivElement) {
        element.querySelector('input')!.click();
    }

    private updateDisabledState(config: AdminConfigType, myImunifyState: IMyImunifyState, isClient: boolean): void {
        if (myImunifyState.enabled && !myImunifyState.protection) {
            this.disableMode = true;
            this.disableMessage = '';
            return;
        }

        const pdClientDisabled = isClient
            && config.PERMISSIONS.user_override_proactive_defense === false;

        this.disableMode = config.PROACTIVE_DEFENCE.php_immunity || pdClientDisabled;
        this.disableMessage = pdClientDisabled
            ? 'warning.set_by_admin'
            : 'warning.php_immunity_enabled';
    }
}
