import { Injectable } from '@angular/core';
import { IPList, IPScope, ListsAPI, ListItem } from '@imunify360-api/lists';
import { NotificationsService } from './notifications';
import { ModalService } from 'app/utils/modals/modal.service';
import { XHR } from 'app/utils/xhr/xhr';

type ActionType = 'added' | 'moved' | 'deleted';

export const PURPOSE  = {
    white: 'utils.table.widgets.content.purpose.values.white',
    drop: 'utils.table.widgets.content.purpose.values.drop',
    captcha: 'utils.table.widgets.content.purpose.values.captcha',
    splashscreen: 'utils.table.widgets.content.purpose.values.splashscreen',
};


@Injectable()
export class ListsService {
    getALL = this.xhr.wrap(this.xhr.rx(ListsAPI.getAllList), ListItem);
    addOrUpdateItems = this.xhr.request(ListsAPI.addOrUpdateItem, false);
    deleteItems = this.xhr.request(ListsAPI.deleteItems);
    // the old editItem endpoint needed for the country comment
    editItem = this.xhr.request(ListsAPI.editItem);

    constructor(
        private modalService: ModalService,
        private notifications: NotificationsService,
        public xhr: XHR,
    ) {}

    changeListWithConfirm(ips: string[],
                          currentListType: IPList | null,
                          nextListType: IPList | null) {
        const params = this.getActionAndRequest(ips, currentListType, nextListType);
        return this.confirmListChangeAndNotify(
            params.request,
            params.action,
            ips,
            params.confirmName,
        );
    }

    async changeListWithConfirmFromIncidents(
        ipLists: Array<{ ip: string, list: IPList | null }>,
        nextListType: IPList | null,
        scope?: IPScope,
    ) {
        const modalAction = this.getActionAndRequest(
            [],
            ipLists[0].list,
            nextListType,
            scope,
        );
        if (!modalAction) {
            return false;
        }
        const items = Array.from(new Set(ipLists.map(item => item.ip)));
        const result = await this.modalService
            .confirm(modalAction.confirmName, items);
        if (result) {
            for (let [list, ips] of this.sortIps(ipLists)) {
                const ipList = Array.from(ips);
                const params = this.getActionAndRequest(ipList, list, nextListType);
                try {
                    await this.confirmListChangeAndNotify(
                        params.request,
                        params.action,
                        ipList,
                        null,
                    );
                } catch (e) {
                }
            }
            if (scope && nextListType) {
                await this.addOrUpdateItems({
                    value: items,
                    scope,
                    list: nextListType,
                });
            }
        }
        return result;
    }

    public sortIps(
        ips: Array<{ ip: string, list: IPList | null }>,
    ): Map<IPList | null, Set<string>> {
        let sortedData = new Map<IPList | null, Set<string>>();
        for (let {list, ip} of ips) {
            let ips = (sortedData.get(list) || new Set());
            ips.add(ip);
            sortedData.set(list, ips);
        }
        return sortedData;
    }

    private getActionAndRequest(
        ips: string[],
        currentList: IPList | null,
        nextList: IPList | null,
        scope?: IPScope,
    ) {
        let action: ActionType;
        let request: () => Promise<any>;
        let confirmName: string;
        if (currentList === null || nextList !== null) {
            confirmName = `add-to-${nextList}`;
            if (currentList === null && nextList !== null) {
                action = 'added';
            } else if (nextList !== null) {
                action = 'moved';
            } else {
                throw new Error('impossible');
            }
            request = () => this.addOrUpdateItems({
                value: ips,
                list: nextList,
            });

        } else {
            action = 'deleted';
            confirmName = `delete-ip-from-list`;
            request = () => this.deleteItems({
                itemType: 'ip',
                items: ips,
                list: currentList,
            });
        }

        if (scope) {
            confirmName += `-${scope}-scope`;
        }
        return {action, confirmName, request};
    }

    private async confirmListChangeAndNotify(
        request: (() => Promise<any>),
        action: ActionType,
        ips: string[],
        confirmName: string | null,
    ) {
        if (!confirmName || await this.modalService.confirm(confirmName, ips)) {
            await request();
            this.notifications.success(
                `notifications.ipAction`,
                null,
                {count: ips.length, action},
            );
            return true;
        } else {
            return false;
        }
    }
}
