import { Component, Inject } from '@angular/core';
import { ProactiveDefenseIgnoreData } from '@imunify360-api/proactive-defense';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ModalEntry } from 'app/utils/modals/entry';

interface IgnoreConfirmModalComponentParams {
    detectedOnly: boolean;
    items: ProactiveDefenseIgnoreData[];
}

@Component({
    template: `
        <i360-modal-content
                confirmText="pd.addToIgnoreModal.confirm"
                id="ignore-confirm-modal">
            <ng-container modal-title>{{ title | translate }}</ng-container>
            <ng-container modal-body>
                <p *ngFor="let item of groupedItems" class="rtl-except break-word">
                    <strong>{{ 'pd.addToIgnoreModal.path' | translate }}</strong>
                     {{ item.path }}<br/>
                    <ng-container *ngIf="item.rules">
                        <strong>{{ 'pd.addToIgnoreModal.rules' | translate }}</strong>
                         {{ item.rules }}
                    </ng-container>
                </p>
            </ng-container>
        </i360-modal-content>
    `,
})
export class IgnoreConfirmModalComponent
    extends ModalEntry<IgnoreConfirmModalComponentParams, false> {
    title: string;
    groupedItems: Array<{path: string, rules?: string}> = [];

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: IgnoreConfirmModalComponentParams,
    ) {
        super();
        this.title = data.detectedOnly
            ? 'pd.addToIgnoreModal.title.detectedOnly'
            : 'pd.addToIgnoreModal.title.all';

        if (data.detectedOnly) {
            const ruleIdToName: {[id: number]: string} = {};
            let rulesByPath: {[path: string]: Set<number>} = {};
            for (let item of data.items) {
                if (!rulesByPath[item.path]) {
                    rulesByPath[item.path] = new Set();
                }
                rulesByPath[item.path].add(item.rule_id!);
                ruleIdToName[item.rule_id!] = item.rule_name!;
            }

            for (let [path, rules] of Object.entries(rulesByPath)) {
                this.groupedItems.push({
                    path,
                    rules: Array.from(rules).map(id => ruleIdToName[id]).join(', '),
                });
            }
        } else {
            const uniquePaths = new Set(data.items.map(item => item.path));
            this.groupedItems = Array.from(uniquePaths).map(path => ({path}));
        }

    }
}
