import { Component, ChangeDetectorRef } from '@angular/core';
import { MyImunifyService } from 'app/services/my-imunify';
import { Subject, noop, takeUntil, take } from 'rxjs';
import { IMyImunifyState } from 'app/services/my-imunify-state';
import { MaliciousService } from 'app/services/malicious';
import { getFixedCount } from 'app/utils/malicious.utils';

@Component({
    selector: 'i360-proactive-defense',
    templateUrl: './proactive-defense.component.html',
    styleUrls: ['./proactive-defense.component.scss'],
})
export class ProactiveDefenseComponent {
    myImunifyState: IMyImunifyState;

    maliciousCount: number;
    maliciousFixed: number;

    private destroy$ = new Subject<void>();

    constructor(
        private cdr: ChangeDetectorRef,
        private myImunifyService: MyImunifyService,
        private maliciousService: MaliciousService,
    ) {
        this.listenMyImunify();
    }

    private listenMyImunify(): void {
        this.myImunifyService.getMyImunifyState()
            .pipe(takeUntil(this.destroy$))
            .subscribe(state => {
                this.myImunifyState = state;

                if (this.myImunifyState.enabled) {
                    this.loadMaliciousCounts();
                }

                this.cdr.markForCheck();
            });
    }

    private loadMaliciousCounts(): void {
        this.maliciousService.list({offset: 0, limit: 100, since: 0})
            .pipe(take(1))
            .subscribe({
                next: (result) => {
                    this.maliciousCount = result?.data?.malicious_count || 0;
                    this.maliciousFixed = getFixedCount(result?.data?.items);
                    this.cdr.markForCheck();
                },
                error: noop
            });
    }
}
