import { Component, OnDestroy, ViewChild, ViewContainerRef } from '@angular/core';
import { Subject, firstValueFrom, switchMap, takeUntil, tap } from 'rxjs';

import {
    ProactiveDefenseIgnoreData,
    ProactiveDefenseItem,
} from '@imunify360-api/proactive-defense';
import { NotificationsService } from 'app/services/notifications';
import { ProactiveDefenseService } from 'app/services/proactive-defense';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { useLoader } from 'app/utils/loader/decorators';
import { ModalService } from 'app/utils/modals/modal.service';
import { RequestDirective } from 'app/utils/table/request/request.directive';
import { DetailsComponent } from '../details/details.component';
import { IgnoreConfirmModalComponent } from '../utils/ignore-confirm-modal.component';

@Component({
    selector: 'i360-proactive-defense-events',
    templateUrl: './events.component.html',
    styleUrls: ['./events.component.scss'],
})
export class ProactiveDefenseEventsComponent implements OnDestroy {
    destroyed = new Subject<void>();
    @ViewChild(LoaderDirective, {static: true}) loader: LoaderDirective;
    @ViewChild(RequestDirective, {static: true}) request: RequestDirective;

    constructor(
        public proactiveDefenseService: ProactiveDefenseService,
        private modalService: ModalService,
        private notifications: NotificationsService,
        private viewContainerRef: ViewContainerRef,
    ) {}

    @useLoader
    async showDetails(id: number) {
        await firstValueFrom(this.proactiveDefenseService.details(id).pipe(
            switchMap(details => this.modalService.open(DetailsComponent, {
                    data: details,
                    panelClass: 'i360-pro-active-details-panel',
                    viewContainerRef: this.viewContainerRef,
                }),
            ),
            switchMap(() => this.request.load()), // listnames may change in details view
            takeUntil(this.destroyed),
        ), { defaultValue: undefined });
    }
    ngOnDestroy() {
        this.destroyed.next();
    }

    @useLoader
    async addToIgnore(items: ProactiveDefenseIgnoreData[], detectedOnly: boolean = false) {
        const confirmed = await this.modalService
            .open(IgnoreConfirmModalComponent, {data: {detectedOnly, items}});
        if (confirmed) {
            items = items.map(({path, rule_id, rule_name}) => ({
                path,
                ...(detectedOnly ? {rule_id, rule_name} : {}),
            }));

            await firstValueFrom(this.proactiveDefenseService.ignoreAdd({items}).pipe(tap(() => {
                this.notifications.success(
                    'pd.events.notifications.successfullyAddedToIgnore',
                    null,
                    {count: items.length},
                );
            })), { defaultValue: undefined });
        }
    }

    @useLoader
    async viewFile(item: ProactiveDefenseItem) {
        await this.modalService.openFileViewer(item.path);
    }
}
