import {
    forwardRef, Component, Inject, Optional, ViewChild, ViewEncapsulation,
} from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { BehaviorSubject } from 'rxjs';

import { IPList } from '@imunify360-api/lists';
import {
    ProactiveDefenseDetails,
    ProactiveDefenseIgnoreData,
} from '@imunify360-api/proactive-defense';
import { AuthState } from 'app/services/auth-state';
import { ListsService } from 'app/services/lists';
import { ProactiveDefenseService } from 'app/services/proactive-defense';
import { useLoader } from 'app/utils/loader/decorators';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { ModalService } from 'app/utils/modals/modal.service';
import { ModalEntry } from 'app/utils/modals/entry';
import { DataLoader, DataLoaderImpl } from 'app/utils/table/data-loader';
import { ProactiveDefenseEventsComponent } from '../events/events.component';


@Component({
    templateUrl: './details.component.html',
    styleUrls: ['./details.component.scss'],
    encapsulation: ViewEncapsulation.None,
    providers: [{
        provide: DataLoaderImpl,
        useExisting: DetailsComponent,
    }],
})
export class DetailsComponent extends ModalEntry<ProactiveDefenseDetails, false>
    implements DataLoader {
    IPList = IPList;
    loaded = new BehaviorSubject([] as any[]);
    @ViewChild(LoaderDirective, {static: true}) loader: LoaderDirective;

    constructor(@Inject(MAT_DIALOG_DATA) public data: ProactiveDefenseDetails,
                private modalService: ModalService,
                public authState: AuthState,
                private proactiveDefenseService: ProactiveDefenseService,
                @Inject(forwardRef(() => ProactiveDefenseEventsComponent))
                public eventsComponent: ProactiveDefenseEventsComponent,
                @Optional() private listsService: ListsService,
        ) {
        super();
        this.loaded.next(data.env);
    }

    @useLoader
    async addToBlackList() {
        if (await this.listsService.changeListWithConfirm(
            [this.data.ip!], this.data.listname, IPList.black,
        ) ) {
            this.data.listname = IPList.black;
        }
    }

    @useLoader
    async addToIgnore(detectedOnly: boolean = false) {
        await this.eventsComponent.addToIgnore(
            [this.data as ProactiveDefenseIgnoreData], detectedOnly);
    }

    @useLoader
    async viewFile() {
        await this.modalService.openFileViewer(this.data.path);
    }
}
