import { Injectable } from '@angular/core';
import { map } from 'rxjs';
import {
    ProactiveDefenseAPI, ProactiveDefenseDetails,
    ProactiveDefenseIgnoreItem, ProactiveDefenseItem,
} from '@imunify360-api/proactive-defense';
import { XHR } from 'app/utils/xhr/xhr';


/**
 * TODO: add scope when migrate to 6
 */
@Injectable()
export class ProactiveDefenseService {
    constructor(public xhr: XHR) {}

    _ignoreList = this.xhr.rx(ProactiveDefenseAPI.ignoreList);
    ignoreList = this.xhr.wrap(this._ignoreList, ProactiveDefenseIgnoreItem);
    ignoreAdd = this.xhr.rx(ProactiveDefenseAPI.ignoreAdd);
    ignoreDeletePaths = this.xhr.rx(
        ProactiveDefenseAPI.ignoreDeletePaths);
    ignoreDeleteRule = this.xhr.rx(ProactiveDefenseAPI.ignoreDeleteRule);

    _list = this.xhr.rx(ProactiveDefenseAPI.list);
    list = this.xhr.wrap(this._list, ProactiveDefenseItem);
    private _details = this.xhr.rx(ProactiveDefenseAPI.details);
    details(id: number) {
        return this._details(id).pipe(
            map(result => new ProactiveDefenseDetails(result.data.items)),
        );
    }
}
