import { NgModule } from '@angular/core';
import { ProactiveDefenseEventsComponent } from './events/events.component';
import { ProactiveDefenseIgnoreListComponent } from './ignore-list/ignore-list.component';
import { ProactiveDefenseComponent } from './proactive-defense.component';
import { ModeComponent } from './mode/mode.component';
import { DetailsComponent } from './details/details.component';
import { ExtendMatChipDirective } from 'app/components/chip/chip.directive';
import { I360NavigationTabsModule } from 'app/components/navigation-tabs/navigation-tabs.module';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { I360LoaderModule } from 'app/utils/loader/loader.module';
import { I360ValidationModule } from 'app/utils/validator/validation.module';
import { I360TableModule } from 'app/utils/table/table.module';
import { I360ModalModule } from 'app/utils/modals/modal.module';
import { I360TranslateModule } from 'app/utils/translate/translate.module';
import { SharedModule } from 'app/core/shared.module';
import { ProactiveDefenseService } from 'app/services/proactive-defense';
import { IgnoreConfirmModalComponent } from './utils/ignore-confirm-modal.component';
import { ProtectionStatusModule } from '../protection-status/protection-status.module';
import { MaliciousService } from 'app/services/malicious';

const publicDirectives = [
    ProactiveDefenseComponent,
    ProactiveDefenseEventsComponent,
    ProactiveDefenseIgnoreListComponent,
];
const privateDirectives = [
    ModeComponent,
    DetailsComponent,
    ExtendMatChipDirective,
    IgnoreConfirmModalComponent,
];

@NgModule({
    imports: [
        RouterModule,
        CommonModule,
        I360LoaderModule,
        I360ValidationModule,
        I360TableModule,
        I360ModalModule,
        I360NavigationTabsModule,
        I360TranslateModule,
        ProtectionStatusModule,
        RouterModule.forChild([{
                path: '',
                component: ProactiveDefenseComponent,
                children: [
                    { path: '', redirectTo: 'events', pathMatch: 'full' },
                    { path: 'events', component: ProactiveDefenseEventsComponent },
                    { path: 'ignore-list', component: ProactiveDefenseIgnoreListComponent },
                ],
            }]),
        SharedModule,
    ],
    declarations: [
        ...publicDirectives,
        ...privateDirectives,
    ],
    providers: [
        ProactiveDefenseService,
        MaliciousService,
    ],
    exports: publicDirectives
})
export class ProactiveDefenseModule {}
