import { Component, TemplateRef, ViewChild } from '@angular/core';

import { ProactiveDefenseIgnoreItem } from '@imunify360-api/proactive-defense';

import { NotificationsService } from 'app/services/notifications';
import { ProactiveDefenseService } from 'app/services/proactive-defense';
import { useLoader } from 'app/utils/loader/decorators';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { ModalService } from 'app/utils/modals/modal.service';
import { RequestDirective } from 'app/utils/table/request/request.directive';
import { firstValueFrom } from 'rxjs';


@Component({
    selector: 'i360-proactive-defense-ignore-list',
    templateUrl: './ignore-list.component.html',
})
export class ProactiveDefenseIgnoreListComponent {
    @ViewChild(LoaderDirective, {static: true}) loader: LoaderDirective;
    @ViewChild(RequestDirective, {static: true}) request: RequestDirective;
    @ViewChild('removeModal', {static: true}) public removeModal: TemplateRef<any>;

    constructor(public proactiveDefenseService: ProactiveDefenseService,
                private modalService: ModalService,
                private notifications: NotificationsService,
                ) {}

    @useLoader
    async deletePaths(items: ProactiveDefenseIgnoreItem[]) {
        const action = 'proactive-remove-from-ignore';
        const paths = items.map(item => item.path);
        if (await this.modalService.confirm(action, paths)) {
            await firstValueFrom(
                this.proactiveDefenseService.ignoreDeletePaths({paths}),
                { defaultValue: undefined }
            );
            this.notifications.success(
                'pd.ignoreList.notifications.successfullyRemoved',
                null,
                {count: items.length},
            );
            await this.request.load();
        }
    }
    @useLoader
    async deleteRule(item: ProactiveDefenseIgnoreItem, rule: {id: number, name: string}) {
        if (await this.modalService.open(this.removeModal, {data: {item, rule}})) {
            await firstValueFrom(
                this.proactiveDefenseService.ignoreDeleteRule({path: item.path, id: rule.id}),
                { defaultValue: undefined }
            );
            this.notifications.success(
                'pd.ignoreList.notifications.successfullyRemoved',
                null,
                {count: 1},
            );
            await this.request.load();
        }
    }
    @useLoader
    async viewFile(item: ProactiveDefenseIgnoreItem) {
        await this.modalService.openFileViewer(item.path);
    }
}
